.login__logo{
    width: 10rem;
}

.login{
    display: grid;
    place-items: center;
    height: 100vh;
    width: 100%;
}

.login  > button{
    width: 20rem;
    background-color: #738adb;
    color: #eff2f5;
    font-weight: 800;
}

.login  > button:hover{
    background-color: #252f53;
    color: #9ccaa0;
}

.login > div> img{
    object-fit: contain;
    height: 10rem;
}
